import React, { useRef, useState } from 'react';
// import { Nav } from './components/nav';
import { Card } from './components/Card';
import { useSwipeable } from 'react-swipeable';


function App() {
  const [cart,setCart] = useState(0);
  const addTocart = ()=>{
    setCart(cart+1);
  }
  const containerRef = useRef(null);
  const handlers = useSwipeable({
    onSwipedUp: (eventData) => {
      containerRef.current.scrollBy({
        top: 200, 
        behavior: 'smooth',
      });
    },
    onSwipedDown: (eventData) => {
      containerRef.current.scrollBy({
        top: -200, 
        behavior: 'smooth',
      });
    },
  });


  const refPassthrough = (el) => {
    handlers.ref(el);
    containerRef.current = el;
  }
  return (
    <div className='my-container' {...handlers}  ref={refPassthrough}>
      {/* <Nav/> */}
      <Card title={`SPLY BAG(${cart})`} description="YZY PODS $11,476.00" whiteModelName="" modelName="" imgPath="https://sply.yeezy.com/cdn/shop/files/Pod_update.gif" first addTocart={addTocart}/>
      <Card title={`SPLY BAG(${cart})`} description="VULTURES PANTS $6,886.00" modelName="black-pants" whiteModelName="white-pants" scale={[0.5,0.5,0.5]} addTocart={addTocart}/>
      <Card title={`SPLY BAG(${cart})`} description="LONG T $5,738.00" modelName="black-tshirt" whiteModelName="white-tshirt" scale={[4,4,4]} addTocart={addTocart}/>
      <Card title={`SPLY BAG(${cart})`} description="VULTURES SHORTS $5,738.00" modelName="black-shorts" whiteModelName="white-shorts" scale={[0.3,0.3,0.3]} addTocart={addTocart}/>
      <Card title={`SPLY BAG(${cart})`} description="BOX T $4,591.00" modelName="black-shirt" whiteModelName="white-shirt" scale={[0.5,0.5,0.5]} addTocart={addTocart}/>
      <Card title={`SPLY BAG(${cart})`} description="ALBUM" modelName="" whiteModelName="" imgPath="https://sply.yeezy.com/cdn/shop/files/Vinyl5.4NoBack.gif" last addTocart={addTocart}/>
      {/* <Card title="SPLY BAG(1)" description="BOX T BR5,860.00" modelName="long-sleeve"/> */}
    </div>
  );
}

export default App;
