import { useEffect, useRef, useState } from "react";
import { Models } from "./models"
import { IconArrowRight } from "@tabler/icons-react";

export const Card =({title,description,modelName,whiteModelName,imgPath=undefined,first=false,last=false,scale=undefined,addTocart}:any)=>{
    const [color,setColor] = useState("black");
    const [size,setSize] = useState(1);
    const [isSizeGuid,setIsSizeGuid] = useState(false);

    const [isOnScreen, setIsOnScreen] = useState(false);
  const targetRef = useRef(null);

  const handleIntersection = (entries:any) => {
    entries.forEach((entry:any) => {
      // Update state based on whether the target is on screen
      setIsOnScreen(entry.isIntersecting);
    });
  };

  useEffect(() => {
    const options = {
      root: null, // Use the viewport as the root
      rootMargin: '0px', // No margin
      threshold: 0.5, // Trigger when 50% of the target is visible
    };

    const observer = new IntersectionObserver(handleIntersection, options);

    if (targetRef.current) {
      observer.observe(targetRef.current);
    }
    return () => {
      observer.disconnect();
    };
  }, []);

  useEffect(()=>{
    if(!isOnScreen)
    setIsSizeGuid(false)
  },[isOnScreen])

    return <div className={"static h-screen	w-screen my-card" } ref={targetRef} >
        {!imgPath && <Models name={color === "black" ? modelName : whiteModelName} size={scale}/>}
        {imgPath && <img src={imgPath} alt="img" height={200} width={300} className="mx-auto"/>}
        {isOnScreen && <div className="absolute bottom-[55px] left-5 text-[#000000] text-sm">
        <p >{title}</p>
        {!last &&<>
         <p className="mt-2">{description}</p>
        {first && <p className="mt-2">DELIVERY WITHIN 4 WEEKS</p>}
        {isSizeGuid && <p className="mt-2">1: XS-S 2: M-L 3: L-XL</p>}
        {!first && <p className="mt-2 cursor-pointer">
            <span className={`cursor-pointer ${color !== "black" ? "text-slate-500" : ""}`} onClick={()=>setColor("black")}>
                BLACK 
                </span>
                <span className={`ml-2 cursor-pointer ${color !== "white" ? "text-slate-500" : ""}`} onClick={()=>setColor("white")}>WHITE</span>
                </p>}
        <p className="mt-2">
          SIZE
          <span className={size === 1 ? "cursor-pointer ml-2" : "cursor-pointer ml-2 text-slate-500 "} onClick={()=>setSize(1)}>1</span>
          <span className={size === 2 ? "cursor-pointer ml-2" : "cursor-pointer ml-2 text-slate-500 "} onClick={()=>setSize(2)}>2</span>
          <span className={size === 3 ? "cursor-pointer ml-2" : "cursor-pointer ml-2 text-slate-500 "} onClick={()=>setSize(3)}>3</span>
          <span className=" text-slate-500  cursor-pointer ml-2" onClick={()=>setIsSizeGuid(!isSizeGuid)}>{isSizeGuid ?"COLLAPSE" :"SIZE GUIDE"}</span>
          </p>
        <p className="mt-2 cursor-pointer flex items-center" onClick={addTocart}>ORDER <IconArrowRight size={14}/></p>
        </>}
        {last && <p className="mt-2 cursor-pointer flex items-center">VULTURES <IconArrowRight size={14}/></p>}
        </div>}
    </div>
}