import { Canvas } from "@react-three/fiber";
import { Gltf, OrbitControls } from "@react-three/drei";

const MyModel = ({name,size}:{name:string,size:any}) => {
  
  return (
    <>
      <mesh>
        <Gltf src={`/yeezy/${name}.glb`} scale={size}/>
      </mesh>
      <OrbitControls enableZoom={false} enablePan={false} maxPolarAngle={Math.PI / 2} minPolarAngle={Math.PI / 2} autoRotate/>
    </>
  );
};

export const Models = ({name,size}:{name:string,size:any}) => {
  
  return (
    <Canvas style={{ height: "500px",pointerEvents:"auto" }}  >
      <ambientLight intensity={1} color="white" />
      <directionalLight intensity={1} position={[5, 5, 5]} />

      <MyModel name={name} size={size}/>
    </Canvas>
  );
};
